import Header from './Header'
import {queryNewsDetail} from '../api/API'
import ScreenUtil from '../utils/ScreenUtil'
export default {
  name: 'NewsContent',
  components: {
    Header
  },
  data () {
    return {
      contentData: {},
      contentStype: {
        width: ScreenUtil.getScreenSize().w + 'px',
        height: ScreenUtil.getScreenSize().h + 'px'
      }
    }
  },
  mounted () {
    window.EventBus.$on('show_news', this.loadData)
    window.addEventListener('resize', this.onWindowResize)
  },
  methods: {
    async loadData (id) {
      this.contentData = {}
      let res = await queryNewsDetail(this.$store.state.User.token, id)
      if (res.code === 200) {
        this.contentData = res.data
      }
    },
    onBackClicked () {
      window.EventBus.$emit('close_news')
    },
    onWindowResize () {
      this.contentStype = {
        width: ScreenUtil.getScreenSize().w + 'px',
        height: ScreenUtil.getScreenSize().h + 'px'
      }
      console.log('resize', this.contentStype)
    }
  }
}



